import {action, observable} from "mobx";

const CONTEXT_URL = process.env.REACT_APP_API_URL || '';

export default class AuthStore {
  @observable
  user = null;

  signIn(username, password) {
    const paramsUser = {
      username: username,
      password: password,
    };
    let formData     = [];
    for (let index in paramsUser) {
      let encodedKey   = encodeURIComponent(index);
      let encodedValue = encodeURIComponent(paramsUser[index]);
      formData.push(encodedKey + '=' + encodedValue);
    }
    formData     = formData.join('&');
    const params = {
      method : 'POST',
      body   : formData,
      headers: {'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'}
    };
    fetch('http://localhost:8080/PizzaService/login', params)
      .then(response => response.json())
      .then(action(user => {
        this.user = user;
        return true;
      }))
      .catch(e => {
        console.log(e);
        return false;
      });
  }

  logOut() {
    fetch(CONTEXT_URL + 'hello', {method: 'POST'})
      .then(() => this.user = null)
      .catch(e => console.log(e));
  }
}


